<template>
    <div class="loading-body">
        <dth-header></dth-header>
        <dth-back-top></dth-back-top>
        <dth-back-home></dth-back-home>
        <div class="loading-blank" v-if="isLoading">打印中。。。。完成打印大约还需<span style="font-size: 4em; color: #2196F3;margin: 0 10px;">{{num}}</span>秒，请耐心等待！</div>
        <div class="loading-blank" v-if="!isLoading">
            <div class="loading-title">打印完成！即将跳转到打印发票页面。。。。</div>
            <!--<el-button type="primary" @click="doVoinceBack">返回</el-button>-->
        </div>
        <dth-footer></dth-footer>
    </div>
</template>
<script>
import DthHeader from '@/components/DthHeader/DthHeader.vue';
import DthFooter from '@/components/DthFooter/DthFooter.vue';
import DthBackHome from "@/components/DthBackHome/DthBackHome.vue";
import DthBackTop from "@/components/DthBackTop/DthBackTop.vue";
export default {
    name: 'Blank',
    components: { DthHeader, DthFooter, DthBackHome, DthBackTop },
    data() {
        return {
            num: 60,
            isLoading: true
        }
    },
    mounted () {
        this.$nextTick(() => {
            const element = document.documentElement;
            if(element.requestFullscreen) {
                
            element.requestFullscreen();
            } else if (element.mozRequestFullScreen){
            element.mozRequestFullScreen();
            } else if(element.webkitRequestFullscreen) {
            element.webkitRequestFullscreen();
            } else if (element.msRequestFullscreen) {
            element.msRequestFullscreen();
            }
        })
        let timer = setInterval(() => {
            this.num--
            if(this.num <=0 ) {
                clearInterval(timer)
                this.isLoading = false
                setTimeout(() => {
                    this.$router.push({path: '/doInvoice'})
                }, 3000)
            }
        }, 1000)
    },
    methods: {
        doVoinceBack() {
            this.$router.push({path: '/doInvoice'})
        }
    },
}
</script>
<style lang="scss" scoped>
.loading-blank {
    font-size: 42px;
}
.loading-body {
    display: flex;
    height: 100%;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(30deg, #fefefe, #f7ebe9, #c1e2e2);
}
.loading-title {
    margin: 20px 0;
    font-size: 16px;
}
</style>